import React from 'react'
import { Typography, Toolbar, AppBar, IconButton } from '@material-ui/core';
import { ArrowBack } from '@material-ui/icons';

function Appbar({ handleBack }) {
    return (
        <AppBar style={{ backgroundColor: '#FFFFFF' }} position="absolute">
            <Toolbar className="appbar">
                <IconButton edge="start" color="inherit" aria-label="menu" onClick={() => handleBack()}>
                    <ArrowBack style={{ color: "black" }} />
                </IconButton>
                <Typography style={{
                    flexGrow: 1,
                    textAlign: 'center',
                    fontSize: 22,
                    color: 'black',
                    marginLeft:-5
                }} variant="h6" >
                    <p style={{ margin: 0 }}>Two Players Game</p>
                </Typography>
            </Toolbar>
        </AppBar>
    )
}

export default Appbar;
