import React from 'react';
import { Grid, Dialog, DialogTitle, Divider, Button, RadioGroup, FormControlLabel, Radio } from '@material-ui/core';

function NumberOfGames({ whoStarts, handleClose, handleWhosTurn }) {
    return (
        <Dialog open={true}  >
            <DialogTitle style={{ textAlign: 'center' }}>Who starts</DialogTitle>
            <div style={{
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'center',
                alignItems: 'center',
                overflowY: 'hidden',
                overflowX: 'hidden', height: 311, width: 250
            }}>
                <div style={{ height: '60%', width: '84%' }}>
                    <RadioGroup name="games" value={whoStarts} onChange={(e) => handleWhosTurn(e)} >
                        <FormControlLabel style={{ width: '100%', marginLeft: 0, marginBottom: 5, borderRadius: 10, backgroundColor: '#EFF3FF' }} value="Alternative turn" control={<Radio />} label="Alternative turn" />
                        <FormControlLabel style={{ marginBottom: 5, marginLeft: 0, borderRadius: 10, backgroundColor: '#EFF3FF' }} value="Looser first" control={<Radio />} label="Looser first" />
                        <FormControlLabel style={{ marginBottom: 5, marginLeft: 0, borderRadius: 10, backgroundColor: '#EFF3FF' }} value="Winner first" control={<Radio />} label="Winner first" />
                        <FormControlLabel style={{ marginBottom: 5, marginLeft: 0, borderRadius: 10, backgroundColor: '#EFF3FF' }} value="Always player 01" control={<Radio />} label="Always player 01" />
                        <FormControlLabel style={{ marginBottom: 5, marginLeft: 0, borderRadius: 10, backgroundColor: '#EFF3FF' }} value="Always player 02" control={<Radio />} label="Always player 02" />
                    </RadioGroup>
                </div>
                <div style={{ position: 'absolute', bottom: 65, height: '1%', width: '100%' }}>
                    <Divider style={{ position: 'relative', top: '5px', marginBottom: 20 }} />
                </div>
                <div style={{ height: '39%', width: '100%' }}>
                    <Grid container spacing={3} style={{ textAlign: 'center', margin: 0, position: 'absolute', bottom: '0px', width: '100%' }}>
                        <Grid item xs={12} sm={6}>
                            <Button
                                onClick={() => handleClose('gKeyWhosTurn')}
                                style={{ marginLeft: 10, fontSize: 14, color: '#4B7BFF', backgroundColor: 'white', borderRadius: 10 }}
                                size="large" variant="contained" color="primary">CANCEL</Button>

                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <Button
                                onClick={() => handleClose('gKeyWhosTurn')}
                                style={{ marginRight: -15, fontSize: 14, color: 'white', backgroundColor: '#4B7BFF', borderRadius: 10 }}
                                size="large" variant="contained" color="primary">OK</Button>
                        </Grid>
                    </Grid>
                </div>

            </div>

        </Dialog >
    )
}

export default NumberOfGames
