import React from 'react';
import { Paper } from '@material-ui/core';

function Player({ background, color, title, player, currentPlayer }) {

    let border = player.name === currentPlayer.name ? "5px solid #FFA200" : "";

    return (
        <Paper style={{ margin: 10, height: 70, marginBottom: 15, backgroundColor: background }}>
            <div style={{ display: 'flex', flexDirection: 'row', width: '100%', height: '100%' }}>
                <div style={{ width: '45%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                    <div style={{ border: border, borderRadius: 45, height: 55 }}>
                        <img alt="" style={{ border: color, borderRadius: 45, padding: '5px 8px 5px 8px', height: '35px', width: '30px', bottom: '165px' }} src={player.photo} />
                    </div>
                </div>
                <div style={{ width: '55%', display: 'flex', flexDirection: 'column', paddingRight: 10 }}>
                    <div style={{ display: 'flex', flexDirection: 'row', height: '100%', width: '100%' }}>
                        <div style={{ width: '50%' }}>
                            <div style={{ display: 'flex', flexDirection: 'column', height: '100%', width: '100%' }}>
                                <div style={{ height: '40%' }}>
                                    <p style={{ color: 'grey', marginBottom: 0, fontSize: 10 }}>{title}</p>
                                </div>
                                <div style={{ height: '50%' }}>
                                    <p style={{ color: '#424242', marginTop: 0, fontSize: 20 }}>{player.name}</p>
                                </div>
                            </div>
                        </div>
                        <div style={{ width: '50%' }}>
                            <div style={{ display: 'flex', flexDirection: 'column', height: '100%', width: '100%' }}>
                                <div style={{ height: '40%' }}>
                                    <p style={{ color: 'grey', marginBottom: 0, fontSize: 10 }}>Score</p>
                                </div>
                                <div style={{ height: '50%' }}>
                                    <p style={{ color: '#424242', marginTop: 0, fontSize: 20 }}>{player.score}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Paper>
    )
}

export default Player
