import React from 'react';
import { IconButton, Button, Divider, Grid, Paper } from '@material-ui/core'
import { PhotoCamera } from '@material-ui/icons';
import { grid, paper2 } from '../styles/screens';

import winner from '../images/winner.png';
import run from '../images/run.png';

function Screen2({ player1, player2,putImage, noOfGames, whoStarts, handleChange, handleClick, selectGame, selectTurn }) {
    return (
        <Grid style={grid}>
            <Paper style={paper2}>
                <Paper style={{ height: 70, marginBottom: 15, backgroundColor: '#DCF6E4' }}>
                    <div style={{ display: 'flex', flexDirection: 'row', width: '100%', height: '100%' }}>
                        <div style={{ width: '20%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>

                            {player1.photo !== "" ?
                                <img style={{ border: '5px solid #37AC5D', borderRadius: 45, padding: '5px 8px 5px 8px', height: '35px', width: '30px', bottom: '165px' }} src={player1.photo} />
                                : <>
                                    <input accept="image/*" style={{ display: 'none' }} id="icon-button-file" type="file" onChange={(e) => putImage(e, 1)} />
                                    <label htmlFor="icon-button-file">
                                        <IconButton color="primary" aria-label="upload picture" component="span">
                                            <PhotoCamera />
                                        </IconButton>
                                    </label>
                                </>
                            }

                        </div>
                        <div style={{ width: '80%', display: 'flex', flexDirection: 'column', paddingRight: 10 }}>
                            <p style={{ color: 'grey', fontSize: 10 }}>Player 01</p>
                            <input
                                name='player1'
                                value={player1.name}
                                className="cinput"
                                onChange={(e) => handleChange(e)}
                                style={{ fontSize: 14, paddingBottom: 5, border: 'none', borderBottom: '0.5px solid lightgray', background: 'transparent' }}
                            />
                        </div>
                    </div>
                </Paper>
                <Paper style={{ height: 70, marginBottom: 15, backgroundColor: '#F6EFD5' }}>
                    <div style={{ display: 'flex', flexDirection: 'row', width: '100%', height: '100%' }}>
                        <div style={{ width: '20%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>

                            {player2.photo !== "" ?
                                <img style={{ border: '5px solid #F8D146', borderRadius: 45, padding: '5px 8px 5px 8px', height: '35px', width: '30px', bottom: '165px' }} src={player2.photo} />
                                : <>
                                    <input accept="image/*" style={{ display: 'none' }} id="icon-button-file" type="file" onChange={(e) => putImage(e, 2)} />
                                    <label htmlFor="icon-button-file">
                                        <IconButton color="primary" aria-label="upload picture" component="span">
                                            <PhotoCamera />
                                        </IconButton>
                                    </label>
                                </>
                            }

                        </div>
                        <div style={{ width: '80%', display: 'flex', flexDirection: 'column', paddingRight: 10 }}>
                            <p style={{ color: 'grey', fontSize: 10 }}>Player 02</p>
                            <input
                                className="cinput"
                                name='player2'
                                value={player2.name}
                                onChange={(e) => handleChange(e)}
                                style={{ fontSize: 14, paddingBottom: 5, border: 'none', borderBottom: '0.5px solid lightgray', background: 'transparent' }} />
                        </div>
                    </div>
                </Paper>
                <Paper style={{ height: 70, marginBottom: 15, backgroundColor: '#EFF3FF' }}>
                    <div style={{ display: 'flex', flexDirection: 'row', width: '100%', height: '100%' }}>
                        <div style={{ width: '20%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                            <img alt="" style={{ border: '5px solid #B1C4F9', borderRadius: 45, padding: '5px 8px 5px 8px', height: '35px', width: '30px', bottom: '165px' }} src={winner} />                        </div>
                        <div style={{ width: '80%', display: 'flex', flexDirection: 'column', paddingRight: 10 }}>
                            <p style={{ color: 'grey', fontSize: 10 }}>Number of games</p>
                            <input
                                className="cinput"
                                value={noOfGames}
                                readOnly
                                onClick={() => selectGame('selectGame')}
                                style={{ fontSize: 14, paddingBottom: 5, border: 'none', borderBottom: '0.5px solid lightgray', background: 'transparent' }} />
                        </div>
                    </div>
                </Paper>
                <Paper style={{ height: 70, marginBottom: 15, backgroundColor: '#EFF3FF' }}>
                    <div style={{ display: 'flex', flexDirection: 'row', width: '100%', height: '100%' }}>
                        <div style={{ width: '20%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                            <img alt="" style={{ border: '5px solid #B1C4F9', borderRadius: 45, padding: '5px 8px 5px 8px', height: '35px', width: '30px', bottom: '165px' }} src={run} />                        </div>
                        <div style={{ width: '80%', display: 'flex', flexDirection: 'column', paddingRight: 10 }}>
                            <p style={{ color: 'grey', fontSize: 10 }}>Who starts</p>
                            <input
                                className="cinput"
                                value={whoStarts}
                                readOnly
                                onClick={() => selectTurn('gKeyWhosTurn')}
                                style={{ fontSize: 14, paddingBottom: 5, border: 'none', borderBottom: '0.5px solid lightgray', background: 'transparent' }} />
                        </div>
                    </div>
                </Paper>
                <Divider style={{ position: 'relative', top: '5px', marginBottom: 20 }} />
                <Button fullWidth onClick={() => handleClick()}
                    style={{ fontSize: 14, backgroundColor: '#4B7BFF', borderRadius: 10 }}
                    size="large" variant="contained" color="primary">Start Game</Button>
            </Paper>
        </Grid>
    )
}

export default Screen2;
