import React from 'react';
import { Button, Divider, Grid, Paper } from '@material-ui/core';
import {  PlayCircleOutline } from '@material-ui/icons';
import { grid, paper1 } from '../styles/screens';

import img1 from '../images/img1.png';
import one from '../images/one.png';
import online from '../images/online.png';
import two from '../images/two.png';
import training from '../images/training.png';

const renderStr = (str) => {
    return str.replace(/\b[a-z]/gi, (char) => char.toUpperCase());
}

function Screen1({ handleClick }) {
    return (
        <Grid style={grid}>
            <Paper style={paper1}>
                <div style={{ position: 'relative', bottom: '84px', width: 110 }}>
                    <p style={{ color: '#3D4276', fontWeight: 'bold', fontSize: 12, marginBottom: 2 }}>CONNECT FOUR!</p>
                    <p style={{ color: '#949494', fontSize: 10, marginTop: 0 }}>Play with other players around the world.</p>
                </div>

                <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', bottom: 250, left: 69, position: 'absolute', backgroundColor: 'rgb(255, 114, 67)', height: 68, width: 70, borderRadius: 12 }}>
                    <p style={{ marginBottom: 0 }}>  <PlayCircleOutline style={{ color: 'white' }} /></p>
                    <p style={{ marginTop: -4, fontWeight: 'bold', color: '#424242' }}>PLAY</p>
                </div>
                <img alt="img1" style={{ position: 'relative', height: '300px', left: '250px', bottom: '165px' }} src={img1} />
                <Divider style={{ position: 'relative', bottom: '200px' }} />
                <Grid container spacing={3} style={{ padding: 30, paddingTop: 10, paddingLeft: 3, width: 428, position: 'absolute', top: '230px' }}>
                    <Grid item xs={12} sm={6}>
                        <Button fullWidth onClick={() => handleClick(false)} startIcon={<img alt="img1" style={{ height: '30px' }} src={one} />}
                            style={{ fontSize: 11, backgroundColor: '#4BABFF', borderRadius: 10 }}
                            size="large" variant="contained" color="primary">{renderStr("Custom Game")}</Button>
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <Button fullWidth onClick={() => handleClick(true)} startIcon={<img alt="img1" style={{ height: '30px' }} src={two} />}
                            style={{ fontSize: 11, backgroundColor: '#4B7BFF', borderRadius: 10 }}
                            size="large" variant="contained" color="primary">{renderStr("Two Players")}</Button>
                    </Grid>
                    <Grid item xs={6} sm={6}>
                        <Button fullWidth onClick={() => handleClick(false)} startIcon={<img alt="img1" style={{ height: '30px' }} src={online} />}
                            style={{ fontSize: 11, backgroundColor: '#4B4BFF', borderRadius: 10 }}
                            size="large" variant="contained" color="primary">{renderStr("Game Online")}</Button>
                    </Grid>
                    <Grid item xs={6} sm={6}>
                        <Button fullWidth onClick={() => handleClick(false)} startIcon={<img alt="img1" style={{ height: '30px' }} src={training} />}
                            style={{ fontSize: 11, backgroundColor: '#6E4BFF', borderRadius: 10 }}
                            size="large" variant="contained" color="primary">{renderStr("Training Game")}</Button>
                    </Grid>
                </Grid>
            </Paper>
        </Grid>
    )
}

export default Screen1;
