import React from 'react';
import { Button, Divider, Grid, Paper } from '@material-ui/core';
import { grid } from '../styles/screens';
import Player from './Player';

function Screen3({ player1, matchDraw, winner, player2, handleUpdatePlayer, handleEndTournament, restartTheGame, showTournamentStatus, tournamentWinner, showSuccess, matrix, noOfGames, playingGame, handleStep, handleUndoStep, currentPlayer, winPattern }) {
    const isPatternMatched = (i, j) => {
        return winPattern && winPattern.filter(d => d.r == i && d.c == j).length == 1
    }

    return (
        <Grid style={grid}>
            <Paper style={{ backgroundColor: '#F5F5F5', borderRadius: 10, position: 'absolute', width: 700, height: 430 }}>
                <div style={{ display: 'flex', flexDirection: 'row', width: '100%' }}>
                    <div style={{ width: '70%' }}>
                        <Paper style={{ height: 400, backgroundColor: '#84A4FC', padding: 15 }}>
                            {matrix.map((rows, i) => {
                                return (
                                    <div key={i} style={{
                                        borderRadius: 20,
                                        opacity: 1,
                                        display: 'flex',
                                        margin: 0,
                                        marginTop: i === 0 ? 0 : 5
                                    }}>
                                        {rows.map((item, j) =>
                                            <div key={j} onClick={() => handleStep(i, j)}
                                                style={{
                                                    cursor: 'pointer',
                                                    background: '#FFFFFF 0% 0% no-repeat padding-box',
                                                    boxShadow: '0px 3px 6px #00000029',
                                                    border: '1px solid #4B7BFF26',
                                                    borderWidth: 1,
                                                    display: 'flex',
                                                    borderRadius: 50,
                                                    alignItems: 'center',
                                                    justifyContent: 'center',
                                                    margin: '2px 12px 2px 4px',
                                                    height: 40,
                                                    width: 40
                                                }}>
                                                {item === player1.name ?
                                                    isPatternMatched(i, j) ?
                                                        <div style={{ border: '4px solid #FFFF00', borderRadius: 45, height: '37px', }}>
                                                            <img alt="" style={{ border: '5px solid #37AC5D', borderRadius: 45, height: '21px', padding: 3, width: '20px', bottom: '165px' }} src={player1.photo} />
                                                        </div>
                                                        : <img alt="" style={{ border: '5px solid #37AC5D', borderRadius: 45, height: '27px', padding: 3, width: '30px', bottom: '165px' }} src={player1.photo} />

                                                    : item === player2.name ?
                                                        isPatternMatched(i, j) ?
                                                            <div style={{ border: '4px solid #FFFF00', borderRadius: 45, height: '37px', }}>
                                                                <img alt="" style={{ border: '5px solid #F8D146', borderRadius: 45, height: '21px', padding: 3, width: '20px', bottom: '165px' }} src={player2.photo} />
                                                            </div>
                                                            : <img alt="" style={{ border: '5px solid #F8D146', borderRadius: 45, height: '27px', padding: 3, width: '30px', bottom: '165px' }} src={player2.photo} />
                                                        : ""}

                                            </div>)}
                                    </div>
                                );
                            })}
                        </Paper>
                    </div>
                    <div style={{ textAlign: 'center', width: '30%' }}>
                        <div style={{ height: '100%', display: 'flex', flexDirection: 'column', margin: 10 }}>
                            <div style={{ padding: 2, color: 'black', fontSize: 17, fontWeight: 'bold' }}>{noOfGames} Games Tournament</div>
                            {
                                matchDraw ?
                                    <p style={{ fontSize: 12, margin: 0, color: '#505351' }}>Match draw !</p>
                                    : showTournamentStatus ?
                                        <div style={{ fontSize: 20, fontWeight: 'bold', color: '#FF6600' }}>
                                            Congratulation!
                         <p style={{ fontSize: 12, margin: 0, color: '#505351' }}>{tournamentWinner}, you won tournament !</p>
                                        </div> :
                                        showSuccess ?
                                            <div style={{ fontSize: 20, fontWeight: 'bold', color: '#FF6600' }}>
                                                Congratulation!
                                <p style={{ fontSize: 12, margin: 0, color: '#505351' }}>{winner}, you won Game {playingGame}</p>
                                            </div>
                                            : <div style={{ color: '#505351', fontSize: 14 }}>Playing Game {playingGame}</div>}

                            <Player
                                background="#DCF6E4"
                                color="5px solid #37AC5D"
                                title="Player 01"
                                player={player1}
                                currentPlayer={currentPlayer}
                            />

                            <Player
                                background="#F6EFD5"
                                color="5px solid #F8D146"
                                title="Player 02"
                                player={player2}
                                currentPlayer={currentPlayer}
                            />

                            <div style={{ margin: '10px 0px 10px 0px' }}><Divider /></div>


                            {showSuccess ?
                                <Button fullWidth onClick={() => handleUpdatePlayer()} style={{ marginTop: 10, marginBottom: 20, backgroundColor: '#4b7bff', color: 'white' }}>Next Game</Button>
                                : showTournamentStatus || matchDraw ?
                                    <Button fullWidth onClick={() => restartTheGame()} style={{ marginBottom: 20, backgroundColor: '#4b7bff', color: 'white' }}>Play Again</Button>
                                    : <Button fullWidth onClick={() => handleUndoStep()} style={{ marginTop: 10, marginBottom: 20, backgroundColor: '#4B7BFF', color: 'white' }}>Undo Step</Button>

                            }


                            <Button fullWidth onClick={() => handleEndTournament()} style={{ backgroundColor: '#FFFFFF', color: 'red' }}>End Tournament</Button>

                        </div>
                    </div>
                </div>
            </Paper>
        </Grid>
    )
}

export default Screen3;
